.Rocket-details-card {
 width: 18rem;
 padding: 1rem;
 margin-bottom: 15px;
}

@media (max-width:700px) {
 .Rocket-details-card{
  width:100%;
 }
}

.Rocket-mission-image {
 background-color: #F2F2F2;
 min-width: 100%;
}

.Rocket-mission-name-flight-number {
